.section-awards-5
	.wrap-items
		display: grid
		gap: 15px
		@media (min-width: 768px)
			grid-template-columns: repeat(2, 1fr)
		@media (min-width: 1024px)
			gap: r(20px)
			grid-template-columns: repeat(3, 1fr)
		.item-panel
			.img
				+img-scale(267/400)
			.content
				padding: r(12px) r(20px)
				text-align: center
				display: flex
				flex-direction: column
				align-items: center
				.title
					font-size: r(20px)
				.line
					background-color: $red
					width: r(60px)
					height: r(1px)
					margin: r(12px) 0px
				.desc
					font-size: r(14px)
					color: $neutral-600
	.img-banner 
		+img-scale(320/1240)
		margin-top: r(80px)
		@media (max-width: 1023.98px)
			margin-top: 40px