footer
	background: #f5f8fb
	.footer-top
		padding: 25px 0
		.logo 
			text-align: center
		.menu-list
			+type-none
			display: flex
			justify-content: center
			align-items: center
			flex-wrap: wrap
			li 
				display: flex
				justify-content: center
				align-items: center
				padding: 15px
				a 
					display: flex
					justify-content: center
					align-items: center
					font-size: r(18px)
					font-weight: 700
					color: #333333
					text-transform: uppercase
					transition: .2s ease-in-out all
					&:hover 
						color: $main
			@media(min-width: 1024px)
				margin-top: 20px
			@media(min-width: 1200px)
				margin-top: 30px
		.main-news-letter
			text-align: center
			margin-top: 15px
			.title 
				font-size: r(20px)
				font-weight: 700
				text-transform: uppercase
				color: #333333
			.sub-title 
				font-size: r(16px)
				font-weight: 400px
				color: #333333
				margin-top: 10px
			.main-form
				margin-top: 15px
			.subscribefrm
				display: flex
				align-items: center
				justify-content: center
				max-width: 440px
				margin-left: auto 
				margin-right: auto 
				flex-wrap: wrap
				.subscribeemail 
					width: calc(100% - 105px)
					height: 40px
					background: #ffffff
					border: 1px solid #ffffff
					padding: 5px 20px
					font-size: 14px
					color: #999999

				.subscribebutton 
					width: 105px
					height: 40px
					display: flex
					justify-content: center
					align-items: center
					background: $main
					color: #ffffff
					text-transform: uppercase
					font-size: r(16px)
					font-weight: 400
					transition: .2s ease-in-out all
					border: 1px solid $main

					&:hover 
						border-color: $red
						background: $red
						color: #ffffff
				span 
					width: 100%
					display: flex
					margin-top: 3px
					color: red 
					font-style: italic
					font-size: 12px
			@media(min-width: 1024px)
				margin-top: 20px
			@media(min-width: 1200px)
				margin-top: 30px
		.social-list
			+type-none
			display: flex
			justify-content: center
			align-items: center
			flex-wrap: wrap
			li 
				display: flex
				justify-content: center
				align-items: center
				margin: 15px
				a 
					display: flex
					justify-content: center
					align-items: center
					font-size: r(16px)
					color: #ffffff
					width: 30px
					height: 30px
					border-radius: 50%
					background: #000000
					transition: .2s ease-in-out all
					&:hover 
						background: $red
						// color: 
		@media(min-width: 1024px)
			padding: 40px 0
		@media(min-width: 1280px)
			padding: 50px 0
			.social-list
				margin-top: 25px
	.footer-bottom 
		background: $main
		color: rgba(#ffffff, 0.6)
		font-size: r(14px)
		font-weight: 100
		text-align: center
		padding: 10px 0
		.ModuleContent
			max-width: 300px
			margin-left: auto 
			margin-right: auto 
