.home-slide
	.main-slide
		margin-top: 15px
	.news-item.news-item-big
		.caption
			.title
				margin-top: 5px
				font-size: r(26px)
				// height: 68px
				// @media(min-width: 1024px)
				// 	height: 72px 
				// @media(min-width: 1280px)
				// 	height: 78px
	.main-button
		display: none
		@media(min-width: 1024px)
			display: flex
			position: absolute
			top: -47px
			right: 0
			align-items: center
			.button-prev, .button-next
				position: relative
				top: auto
				left: auto
				right: auto
				transform: none
				display: flex
				justify-content: center
				align-items: center
				width: 25px
				height: 25px
				border: 1px solid #999999
				opacity: 1
				em
					font-size: r(14px)
					color: #999999
					transition: .2s ease-in-out all
					line-height: 1
				&:hover
					border-color: $main
					background: $main
					em
						color: #ffffff
			.button-prev
				margin-right: 5px
	@media(min-width: 1280px)
		padding-bottom: 40px
.home-news
	padding-bottom: 30px
	.line-top
		border-top: 1px solid #f5f8fb
	.main-title
		margin-top: 20px
	
	.news-menu-list
		+type-none
		display: flex
		align-items: center
		margin-top: 20px
		li
			display: flex
			justify-content: center
			align-items: center
			display: none
			+ li
				margin-left: 15px
			a
				font-size: r(16px)
				font-weight: 400
				color: #666666
				display: flex
				justify-content: center
				align-items: center
				transition: .2s ease-in-out all
				&:hover
					color: $main
			&.view-all
				display: flex
				a
					color: $main
					em
						padding-left: 5px
		@media(min-width: 768px)
			li
				display: flex
	.news-head
		display: flex
		justify-content: space-between
		align-items: center
	.news-left, .news-right, .banner-qc-list
		margin-top: 20px
		@media(min-width: 1200px)
			margin-top: 25px
	.banner-qc-list
		margin-top: 20px
		@media(min-width: 1024px)
			margin-top: 20px
	.news-list-block-1
		padding-bottom: 20px
		.news-wrap
			.news-left
				display: none
				&:first-child
					display: block
				.news-item
				+ .news-item
					margin-top: 20px
					display: none
			.news-right
				.news-item-child
					margin-top: 16px
					display: none
					.caption
						.title
							line-height: 1.3
					&:first-child, &:nth-child(2), &:nth-child(3), &:nth-child(4)
						display: flex
		@media(min-width: 1024px)
			.news-wrap
				.news-right
					.news-item-child
						margin-top: 0
						+ .news-item-child
							margin-top: 16px
		@media(min-width: 1280px)
			padding-bottom: 30px
	.news-list-block-2
		padding-top: 10px
		padding-bottom: 10px
		.news-item
			margin-top: 12px
			&.news-item-child
				margin-top: 15px
			@media(min-width: 1200px)
				margin-bottom: 20px
				&.news-item-child
					margin-bottom: 0
			@media(min-width: 1280px)
				.caption
					.title
						height: 54px
					.des
						height: 84px
		.list-dining-child
			> *
				display: none
				&:first-child
					display: block
				&:nth-child(2)
					display: block
		.banner-qc-list
			margin-top: 15px
		.news-list-wine-cellar
			.row
				> *
					display: none
					&:first-child
						display: block
					&:nth-child(2)
						display: block
			.news-item

				.caption
					.title
						height: 27px
		.news-list-knowledge
			.list-knowledge-child
				> *
					display: none
					&:first-child
						display: block
					&:nth-child(2)
						display: block
					&:nth-child(3)
						display: block
		@media(min-width: 1280px)
			padding-top: 30px
			padding-bottom: 30px
			.main-title
				margin-top: 0
			.news-menu-list
				margin-top: 0
			.banner-qc-list
				margin-top: 0
			.news-list-wine-cellar
				.news-item
					margin-bottom: 0
			.news-list-knowledge
				.news-item
					margin-bottom: 0
	.banner-full
		margin-top: 20px
		img
			width: 100%
			max-height: 100%
			max-width: 100%
			object-fit: contain
		@media(min-width: 1024px)
			margin-top: 30px

	.news-list-block-3
		padding: 20px 0
		.news-body
			> *
				display: none
				&:first-child, &:nth-child(2), &:nth-child(3), &:nth-child(4)
					display: block
		.news-item
			margin-top: 20px
			.image
				+img-scale(195/295)
			.caption
				.des
					+line(4)
			@media(min-width: 1200px)
				margin-top: 25px
			@media(min-width: 1280px)
				.caption
					.title
						height: 54px
		@media(min-width: 1280px)
			// padding: 30px 0
			padding-top: 35px
			padding-bottom: 20px
	@media(min-width: 1024px)
		padding-bottom: 50px
	@media(min-width: 1200px)
		padding-bottom: 60px
.news-video
	margin-top: 20px
	.news-item
		&.news-item-video
			&.news-item-video-big
				.image
					margin-top: 20px
					.icon-play
						&:before
							width: 30px
							height: 30px
						em
							font-size: r(50px)
						@media(min-width: 1200px)
							&:before
								width: 45px
								height: 45px
							em
								font-size: r(70px)
					@media(min-width: 1024px)
						margin-top: 0
						+img-scale(360/610)
				.caption
					padding: 15px
					.title
						font-size: r(26px)
						margin-top: 0
					.des
						display: inline-block
						font-size: r(18px)
						line-height: 1.3
						> *
							font-size: r(18px)
					@media(min-width: 1024px)
						padding: 30px
						padding-top: 65px
		&.news-item-child
			&.news-item-video
				.caption
					padding: 7px 0
					padding-left: 15px
					.title
						font-size: r(14px)
						margin-top: 0
				.image
					width: 120px
					height: 75px
					min-width: 120px
					padding-top: 0
				+ .news-item-video
					margin-top: 20px
	.news-body
		background: #f0f0f5
		margin-top: 20px
		padding: 25px 0
		.news-list
			margin-top: 20px
		@media(min-width: 1200px)
			padding-top: 26px
			padding-bottom: 32px
			.news-list
				margin-top: 0
	@media(min-width: 1200px)
		margin-top: 30px
	@media(min-width: 1280px)
		margin-top: 33px
.banner-qc-list
	a
		display: block
		width: 100%
	img
		width: 100%
		height: auto
.news-list-block-2
	a
		display: block
		width: 100%
		img
			width: 100%