.section-awards-3
	.row
		row-gap: 20px
	.item-award-category
		position: relative
		box-shadow: 0px 4px 40px 0px rgba(0, 0, 0, 0.04)
		&:hover
			.title
				color: $color-primary
			.btn-view-more
				img
					transform: translateX(4px)
		.img
			+img-scale(440/400)
		.content
			padding: r(24px) r(20px)
			display: flex
			flex-direction: column
			gap: r(12px)
			.title
				font-size: 20px
				font-weight: bold
			.desc
				ul
					padding-left: 0
					list-style: none
					font-size: r(14px)
					color: #656565
					@media (max-width: 1023.98px)
						font-size: 14px
			.btn-view-more
				display: flex
				align-items: center
				font-size: r(14px)
				color: $color-primary
				gap: r(12px)
				img
					transition: .2s ease-in-out all
.popup-modal-award-cate
	max-width: r(876px)
	width: 100%
	.title-40
		text-align: center
	.fancybox-button
		svg
			path
				fill: $color-primary-3
	.format-content
		max-height: r(600px)
		overflow-y: auto
		padding-right: r(20px)
		&::-webkit-scrollbar
			width: 4px
		&::-webkit-scrollbar-track
			background: $neutral-200
		&::-webkit-scrollbar-thumb
			background: $color-primary-3
		table
			width: 100%
			tr
				&:nth-child(odd)
					background-color: #F5F5F8
				&:first-child
					td
						background-color: $color-primary
						color: white
						font-weight: bold
						padding: r(12px) r(12px)
				td
					padding: r(12px) r(12px)
					border: 1px solid $neutral-200
					&:first-child
						width: 60px
						text-align: center