.section-awards-10
	background-color: #50000C
	.wrap-slide
		color: white
	.item-slide
		display: flex
		flex-direction: column
		gap: r(20px)
		.img
			+img-scale(504/393)
		.content
			display: flex
			flex-direction: column
			gap: r(8px)
			align-items: center
			text-align: center
			.title
				font-size: r(20px)
			.desc
				font-size: 14px
		.line-star
			display: flex
			align-items: center
			justify-content: center
			gap: r(10px)
			&::before,&::after
				content: ""
				width: r(60px)
				height: 1px
				background-color: white
				