.om-events
	padding-top: 0
	.main-title
		margin-top: 30px
	.news-list
		margin-top: 30px
		@media(min-width: 1200px)
			margin-top: 35px
	.banner-qc-list
		margin-top: 30px
		@media(min-width: 1200px)
			margin-top: 35px
	.banner-full
		margin-top: 30px
		@media(min-width: 1200px)
			margin-top: 35px
	.news-item
		margin-top: 30px
		@media(min-width: 1200px)
			margin-top: 35px
	.row
		.news-list
			margin-top: 0
	.news-item
		.caption
			.des
				margin-top: 10px
		@media(min-width: 768px)
			&.news-item-big
				display: flex
				background: #f3f3f3
				align-items: center
				.image
					height: 280px
					width: 400px
					padding-top: 0
				.caption
					width: calc( 100% - 400px )
					padding: 20px
					.title
						margin-top: 0
					.des
						+line(5)
					// height: 280px
		@media(min-width: 1024px)
			&.news-item-big
				.image
					height: 350px
					width: 400px
				.caption
					width: calc( 100% - 400px )
			.caption
				.title
					+line(2)
				.des
					+line(3)
			&:not(.news-item-big)
				.caption
					.title
						height: 54px
		@media(min-width: 1200px)
			&.news-item-big
				.image
					height: 380px
					width: 505px
				.caption
					width: calc( 100% - 505px)
		@media(min-width: 1200px)
			&.news-item-big
				.image
					height: 387px
	.news-list-pages
		@media(min-width: 1280px)
			.news-item
				.caption
					.title
						height: auto
						margin-top: 15px
