.om-video-detail
	.news-video-detail-top
		background: #f0f0f5
		.image
			+img-scale(490/820)
			.icon-play 
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%, -50%)
				z-index: 11
				// width: 40px
				// height: 40px
				border-radius: 50%
				// background: #ffffff
				display: flex
				justify-content: center
				align-items: center
				pointer-events: none
				&::before
					content: ''
					position: absolute
					top: 50%
					left: 50%
					transform: translate(-50%, -50%)
					width: 40px
					height: 40px
					border-radius: 50%
					background: $main
					transition: .2s ease-in-out all
					opacity: 0
					z-index: 1
				em 
					font-size: r(60px)
					color: rgba(#ffffff, 0.9)
					transition: .2s ease-in-out all
					position: relative
					z-index: 11
			&:hover
				.icon-play
					&::before
						opacity: 1
					em 
						color: rgba(#ffffff, 1)
		.box-content
			padding: 20px 
		.main-title
			font-size: r(30px)
			font-weight: 700
			color: $main
			+font-2 
			text-transform: none
		.info-title 
			display: flex
			align-items: center
			margin-top: 7px
		.sub-title, time
			font-size: r(16px)
			font-weight: 400
			color: #999999
			display: block
			position: relative
			padding-right: 8px
			margin-right: 8px
			&::before
				content: ''
				position: absolute
				top: 50%
				right: 0
				transform: translateY(-50%)
				background: #999999
				width: 1px
				height: 15px
		.full-content
			margin-top: 15px
			padding: 15px 0
			font-size: r(18px)
			font-weight: 400
			color: #666666
			border-top: 1px solid #e1e1e1
			> * 
				font-size: r(18px)
				font-weight: 400
				color: #666666
		.share-social
			display: flex
			// justify-content: center
			align-items: center
			.jssocials-shares
				display: flex
				justify-content: center
				align-items: center
			.jssocials-share
				display: flex
				justify-content: center
				align-items: center
				margin-right: 20px
				a 
					color: #ffffff
					transition: .2s ease-in-out all
					padding: 0
					width: 40px
					height: 40px
					border-radius: 50%
					background: #2c3442
					display: flex
					justify-content: center
					align-items: center
					&:hover 
						background: $red
		@media(min-width: 1024px)
			align-items: center
			.box-content
				padding-left: 50px
	.banner-full
		margin-top: 20px
		img 
			width: 100%
			max-height: 100%
			max-width: 100%
			object-fit: contain
		@media(min-width: 1024px)
			margin-top: 30px
	.news-video-other
		margin-top: 30px
		.main-title
			font-size: r(30px)
			font-weight: 700
			color: $main
			+font-2
			text-transform: none
		.main-slide
			margin-top: 30px
			.main-button
				.button-prev, .button-next 
					width: 40px
					height: 40px
					border-radius: 50%
					display: flex
					justify-content: center
					align-items: center
					border: 1px solid #e2e2e2
					transition: .2s ease-in-out all
					em 
						font-size: r(24px)
						color: #999999
						line-height: 1
		@media(min-width: 1200px)
			.main-slide
				.main-button
					.button-prev
						right: 102%
					.button-next 
						left: 102%
	.news-item.news-item-video
		padding: 10px
		background: #f0f0f5
		.image
			+img-scale(225/380)
