.main-nav-mb
	position: sticky
	z-index: 950
	top: 80px
	background: #ffffff
	.toggle
		display: flex !important
		justify-content: space-between
		align-items: center
		padding: 10px 15px
		font-size: r(20px)
		font-weight: 400
		color: #666666
		text-transform: uppercase
		border-bottom: 1px solid #e1e1e1
		cursor: pointer
		em
			display: flex
			justify-content: center
			align-items: center
			transition: .2s ease-in-out all
		&.active
			em
				transform: rotate(180deg)
		@media(min-width: 1024px)
			display: none !important
	@media(max-width: 576px)
		top: 85px !important
.about-nav
	border-bottom: 1px solid #e1e1e1
	+type-none
	// display: none
	li
		a
			font-size: 16px
			font-weight: 400
			color: #666666
			transition: .2s ease-in-out all
			text-transform: uppercase
			padding: 10px 15px
			display: block
		&:hover, &.active
			a
				color: $main
				text-shadow: 0.5px 0 0 rgba($main, 1)
	@media(min-width: 1024px)
		display: flex !important
		justify-content: space-around
		align-items: center
		border-top: 1px solid #e1e1e1
		li
			display: flex
			justify-content: center
			align-items: center
			a
				display: flex
				justify-content: center
				align-items: center
				text-align: center
				font-size: r(20px)
	@media(min-width: 1280px)
		justify-content: center
		li
			margin: 0 30px
.om-about
	.about-title
		font-size: r(30px)
		font-weight: 700
		color: #333333
		+font-2
	.about-list
		margin-top: 20px
		.about-item
			margin-bottom: 20px
			&:last-child
				margin-bottom: 0
			.full-content
				margin-top: 20px
				color: #666666
				// > *
					// font-size: r(18px)
					// font-weight: 400
					// color: #666666
				*
					margin-bottom: 20px
					margin-top: 0
					color: #666666
					font-size: r(18px)
				p, a , ul, ol
					max-width: 1040px
					margin-left: auto
					margin-right: auto
				.row
					margin-bottom: 0
					.col-12
						text-align: center
					img
						margin-bottom: 0
					> *
						margin-bottom: 20px
						margin-top: 0
						.row
							margin-bottom: 0
							> *
								&:last-child
									margin-bottom: 0
	.om-about-1
		padding-bottom: 0
	.om-about-2
		padding-top: 0
		padding-bottom: 0
	.om-about-3
		padding-bottom: 0
		padding-top: 0
	.om-about-4
		padding-bottom: 0
		padding-top: 0
	.om-about-5
		padding-top: 20px
		.row
			> *
				margin-top: 20px
		img
			width: 100%
	@media(min-width: 1024px)
		padding-bottom: 30px
	@media(min-width: 1200px)
		padding-bottom: 40px
.box-contact
	max-width: 1040px
	margin-left: auto
	margin-right: auto
	background: #f5f8fb
	border-left: 2px solid $main
	padding: 15px
	.contact-list
		margin-top: 20px
		li
			display: flex
			margin-top: 12px
			.title
				font-size: r(18px)
				font-weight: 700
				color: #333333
				width: 100px
				padding-right: 10px
				em
					color: #2c3442
					padding-right: 15px
			.content
				font-size: r(18px)
				font-weight: 400
				color: #666666
				width: calc( 100% - 100px )
				> *
					font-size: r(18px)
					font-weight: 400
					color: #666666
				a
					+hover-underline(95%, #666666)
	@media(min-width: 768px)
		.contact-list
			li
				.title
					width: 150px
				.content
					width: calc( 100% - 150px )
	@media(min-width: 1024px)
		padding: 20px
	@media(min-width: 1200px)
		padding: 25px 30px
