.om-events-detail
	.box-events-detail
		.detail-title
			font-size: r(30px)
			font-weight: 700
			text-transform: none
			color: $main
			+font-2
		.info-title 
			display: flex
			align-items: center
			margin-top: 20px
		.sub-title, time
			font-size: r(16px)
			font-weight: 400
			color: #999999
			display: block
			position: relative
			padding-right: 8px
			margin-right: 8px
			&::before
				content: ''
				position: absolute
				top: 50%
				right: 0
				transform: translateY(-50%)
				background: #999999
				width: 1px
				height: 15px
		.share-social
			display: flex
			justify-content: center
			align-items: center
			.jssocials-shares
				display: flex
				justify-content: center
				align-items: center
			.jssocials-share
				display: flex
				justify-content: center
				align-items: center
				a 
					color: #999999
					padding: 0
					width: 40px
					background: #ffffff
					display: flex
					justify-content: center
					align-items: center
		.full-content
			margin-top: 20px
			border-top: 1px solid #e1e1e1
			padding: 20px 0
			font-size: r(18px)
			// font-weight: 400
			color: #666666
			* 
				font-size: r(18px)
				// font-weight: 400
				color: #666666
			> *
				font-size: r(18px)
				// font-weight: 400
				color: #666666
			h3, h4, h5, h6
				font-size: 22px
				font-weight: 700
				color: #333333
				+font-2
			hr 
				// color: $main
				// height: 2px
				// background: $main
				border-top: 2px solid $main
			.row
				> *
					margin-top: 0
					margin-bottom: 20px
			p 
				img 
					+ img 
						margin-left: 20px
			figcaption
				font-style: italic
				font-size: r(16px)
		.tags 
			padding-bottom: 20px 
			display: flex
			flex-wrap: wrap 
			margin-bottom: -5px
			
			a 
				font-size: r(18px)
				font-weight: 400
				color: #666666
				margin-right: 20px
				position: relative
				margin-bottom: 5px
				+hover-underline(95%, #666666)
				&:before
					content: '/'
					position: absolute
					top: 50%
					transform: translateY(-50%)
					right: -13px
				&:last-child
					&:before 
						display: none
			p 
				font-size: r(18px)
				font-weight: 700
				color: #333333
				padding-right: 10px
		.box-related
			border-top: 2px solid $main
			padding: 20px 0
			.main-title
				text-transform: none 
				font-size: r(30px)
				font-weight: 700
				color: $main
			.main-slide
				margin-top: 20px
				.main-button
					.button-prev, .button-next 
						width: 40px
						height: 40px
						border-radius: 50%
						display: flex
						justify-content: center
						align-items: center
						border: 1px solid #e2e2e2
						transition: .2s ease-in-out all
						em 
							font-size: r(24px)
							color: #999999
							line-height: 1
			.news-item
				.image
					+img-scale(190/288)
		
		@media(min-width: 1024px)
			padding-right: 20px
			.info-title 
				margin-top: 30px
			.box-related
				padding: 30px 0
				.main-slide
					margin-top: 30px
			.full-content
				margin-top: 25px
				padding: 24px 0
			.tags 
				padding-bottom: 30px
		@media(min-width: 1200px)
			// .full-content
			// 	padding-bottom: 40px
			.tags 
				padding-bottom: 40px
			.box-related
				.main-slide
					margin-right: 10px
					.main-button
						.button-prev
							right: 102%
						.button-next 
							left: 102%
	.box-category
		border: 1px solid #e1e1e1
		.main-title
			font-size: r(20px)
			color: $main
			padding: 14.5px 20px
			background: #f8f8f8
			border-bottom: 1px solid #e1e1e1
		.menu-list
			padding: 0 20px
			li 
				a 
					font-size: r(18px)
					font-weight: 400
					color: #666666
					transition: .2s ease-in-out all
					display: block
					padding: 11.5px 0
				+ li 
					border-top: 1px solid #e1e1e1
				&:hover, &.active
					a 
						color: $main
						text-shadow: 0.5px 0 0 $main
	.box-great
		margin-top: 20px
		border: 1px solid #e1e1e1
		background: #f5f8fb
		padding: 20px
		.great-list 
			.great-item
				display: flex
				margin-top: 20px
				.image
					width: 80px
					height: 53px
					overflow: hidden
					img 
						width: 100%
						height: 100%
						object-fit: cover
				.caption
					width: calc(100% - 80px)
					padding-left: 14px
					.title 
						display: block
						font-size: r(13px)
						color: #333333
						+font-2
						font-weight: 700
				&:hover 
					.caption
						.title
							color: $main
	.banner-list
		margin-top: 20px
	.box-contact 
		margin-bottom: 30px
