header
	position: fixed
	width: 100%
	top: 0
	left: 0
	right: 0
	z-index: 999
	background: #ffffff
	&.active
		box-shadow: 1px 0 20px 0 rgba(0,0,0,0.1)
	.top-wrap
		padding-top: 12px
		padding-bottom: 17px
		background: #ffffff
		.main-wrap
			display: flex
			justify-content: space-between
			gap: 20px
		.left-wrap
			margin: 0 auto
			@media (max-width: 1199.98px)
				width: auto
			@media (max-width: 1023.98px)
				width: 40%
			@media (max-width: 767.98px)
				width: 50%
				
			.logo
				height: 100%
				img
					height: 100%
					@media (max-width: 767.98px)
						height: r(60px)
				.ModuleContent
					height: 100%
					a
						height: 100%
						display: block
						img
							height: 100%
							@media (max-width: 767.98px)
								height: 100%
			@media(min-width: 1200px)
				display: block
		.right-wrap
			// width: calc(100% - 220px)
			display: flex
			.image
				text-align: center
				display: flex
				justify-content: center
				align-items: center
				@media(min-width: 1200px)
					justify-content: flex-end
			@media(max-width: 1200px)
				width: 100%
			@media(max-width: 576px)
				.image
					height: auto
		@media(max-width: 1200px)
			padding: 5px 0
	.bottom-wrap 
		background: $darkGray
		color: #ffffff
		.main-wrap 
			display: flex
			justify-content: space-between
			align-items: center
		.right-wrap
			display: flex
			justify-content: flex-end
			align-items: center
		.left-wrap 
			margin: 0 auto
			.logo
				display: none
				@media(max-width: 1200px)
					display: block
					img 
						height: 35px
			@media(max-width: 576px)
				.main-menu
					display: none
		@media(max-width: 1200px)
			padding: 5px 0
	.menu-list
		+type-none
		display: flex
		align-items: center
		> li 
			display: flex
			align-items: center
			> a 
				display: flex
				justify-content: center
				align-items: center
				font-size: r(13px)
				font-weight: 400
				color: #ffffff
				text-transform: uppercase
				padding: 10.5px 15px
				transition: .2s ease-in-out all
				background: $darkGray
			&:first-child
				> a 
					font-size: 0
					padding: 0 18px
					// width: 60px
					height: 40px
					max-width: 60px
					max-height: 40px
					&::before
						font-family: 'Material Design Icons'
						content: '\F02DC'
						font-size: r(24px)
						color: #ffffff
						line-height: 1
			&:hover, &.active
				> a 
					background: $red
			&.has-dropdown
				position: relative
				&:hover
					.sub-menu
						left: auto
						opacity: 1
						transition-delay: 150ms
						pointer-events: auto
						height: auto
						overflow: visible
		@media(min-width: 1280px)
			> li 
				&:first-child
					margin-right: 20px
	.sub-menu 
		position: absolute
		width: max-content
		background: white
		padding: 0
		display: block
		box-shadow: 1px 1px 0 rgba(0,0,0,.1)
		position: absolute
		left: -99999px
		opacity: 0
		z-index: 99999
		text-align: left
		top: 100%
		transition: opacity 80ms linear
		list-style: none
		.icon-dropdown
			display: none
		li
			> a 
				display: flex
				justify-content: center
				align-items: center
				font-size: r(13px)
				font-weight: 400
				color: $darkGray
				text-transform: uppercase
				padding: 8px 15px
				transition: .2s ease-in-out all
				&:hover
					color: $red
			&.active
				> a
					color: $red
	.button-search
		display: flex
		justify-content: center
		align-items: center
		padding-left: 10px
		cursor: pointer
		em 
			font-size: r(18px)
			color: #ffffff
	.main-language
		margin-left: 22px
		.dropdown-toggle
			width: 20px
			height: 20px
			display: flex
			justify-content: center
			align-items: center
			overflow: hidden
			border-radius: 50%
			img 
				width: 100%
				height: 100%
				object-fit: cover
		.dropdown-menu
			min-width: 42px
			padding: 10px
			.item
				width: 20px
				height: 20px
				display: flex
				justify-content: center
				align-items: center
				overflow: hidden
				img 
					width: 100%
					height: 100%
					object-fit: cover
				+ .item
					margin-top: 10px
		@media(max-width: 1200px)
			margin-left: 20px
	.searchbox
		display: none
	.button-hambuger
		display: none
		@media(max-width: 1200px)
			+dc
			position: relative
			width: 26px
			height: 26px
			margin-left: 20px
			.burger-bt-inner
				width: 26px
				height: 20px
				position: absolute
				cursor: pointer
				span
					content: ""
					display: block
					width: 100%
					height: 2px
					background-color: #ffffff
					position: absolute
					top: 50%
					margin-top: -1px
					transition: opacity .2s cubic-bezier(.165,.84,.44,1)
					opacity: 1
				&:before, &:after
					content: ""
					display: block
					width: 100%
					height: 2px
					background-color: #ffffff
					position: absolute
					-webkit-transition: -webkit-transform .5s cubic-bezier(.165,.84,.44,1)
					transition: -webkit-transform .5s cubic-bezier(.165,.84,.44,1)
					transition: transform .5s cubic-bezier(.165,.84,.44,1)
					transition: transform .5s cubic-bezier(.165,.84,.44,1),-webkit-transform .5s cubic-bezier(.165,.84,.44,1)
				&:before
					top: 0
				&:after
					bottom: 0
				&.active
					span
						opacity: 0
					&:before
						transform: translateY(9px) rotate(45deg)
					&:after
						transform: translateY(-9px) rotate(-45deg)
	.backdrop
		position: fixed
		width: 100%
		opacity: 0
		pointer-events: none
		height: 100%
		transition: .5s ease-in-out
		background: rgba(0,0,0,.8)
		top: 0
		left: 0
		z-index: 31
		&.active
			opacity: 1
			pointer-events: initial
	.mobile-wrap
		display: none
		@media(max-width: 1200px)
			position: fixed
			top: 0
			right: 0
			bottom: 0
			background-color: #ffffff
			margin-top: 0
			width: 260px
			height: 100vh
			display: block
			transform: translateX(260px)
			transition: transform .8s cubic-bezier(.165,.84,.44,1),-webkit-transform .8s cubic-bezier(.165,.84,.44,1)
			z-index: -1
			max-width: 260px
			padding-top: 0
			opacity: 0
			&::before
				content: ''
				position: absolute
				top: 0
				left: 0
				background: $darkGray
				width: 1px
				height: 100%
				pointer-events: none
			.menu-list
				display: block
				+type-none
				flex-wrap: wrap 
				width: 260px
				max-width: 260px
				justify-content: flex-start
				margin: 0
				padding-top: 140px
				> li 
					padding: 7px 30px
					display: block
					> a 
						padding: 3px 0
						background: #ffffff
						color: $darkGray
						text-align: left
						justify-content: flex-start
						display: block
					&:first-child
						> a 
							&:before 
								color: $main
					&.active, &:hover 
						> a 
							+hover-underline(95%, $main)
			.icon-dropdown
				position: absolute 
				top: 0
				right: 10px
				z-index: 1
				height: 38px
				width: 38px
				display: flex
				justify-content: center
				align-items: center
				&:before
					font-family: 'Linearicons-Free'
					content: '\e874'
					font-size: r(18px)
					color: $darkGray
					line-height: 1
			.sub-menu
				position: static
				width: 100%
				left: 0
				opacity: 1
				box-shadow: none
				padding-left: 10px
				display: none
				> li
					> a
						display: block
						padding: 5px
			&.active
				transform: translateX(0)
				opacity: 1
		@media(max-width: 576px)
			.menu-list
				padding-top: r(120px)
