.news-item
	.image
		height: 0
		+img-scale(285/450)
	.caption
		.category
			font-size: r(14px)
			font-weight: 700
			color: $main
			text-transform: uppercase
			margin-top: 15px
		.title
			+font-2
			margin-top: 10px
			font-size: r(18px)
			font-weight: 700
			color: #333333
			transition: .2s ease-in-out all
			// display: inline-block
			+line(2)
			&:hover
				color: $main
		.des
			margin-top: 20px
			font-size: r(14px)
			font-weight: 400
			color: #666666
			+line(4)
			text-align: justify
			padding-right: 15px
			> *
				font-size: r(14px)
				font-weight: 400
				color: #666666
				text-align: justify
			@media(min-width: 1200px)
				margin-top: 25px
				padding-right: 20px
			@media(min-width: 1366px)
				padding-right: 25px
	&:hover
		.image
			img
				transform: scale(1.1)
	&.news-item-child
		display: flex
		.image
			width: 160px
			height: 100px
			min-width: 160px
			padding-top: 0
		.caption
			padding-left: 20px
			padding-top: 7px
			width: calc(100% - 160px)
			.title
				font-size: 16px
				+line(2)
	&.news-item-big
		.image
			+img-scale(385/610)
		.caption
			.des
				font-size: r(18px)
				> *
					font-size: r(18px)
	&.news-item-video
		.image
			height: 0
			+img-scale(365/610)
			.icon-play
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%, -50%)
				z-index: 11
				// width: 40px
				// height: 40px
				border-radius: 50%
				// background: #ffffff
				display: flex
				justify-content: center
				align-items: center
				pointer-events: none
				&::before
					content: ''
					position: absolute
					top: 50%
					left: 50%
					transform: translate(-50%, -50%)
					width: 20px
					height: 20px
					border-radius: 50%
					background: $main
					transition: .2s ease-in-out all
					opacity: 0
					z-index: 1
				em
					font-size: r(32px)
					color: rgba(#ffffff, 0.9)
					transition: .2s ease-in-out all
					position: relative
					z-index: 11
		&:hover
			.image
				.icon-play
					&::before
						opacity: 1
					em
						color: rgba(#ffffff, 1)
.banner-item
	text-align: center
	img
		max-width: 100%
		max-height: 100%
		object-fit: contain
	+ .banner-item
		margin-top: 20px
	@media(max-width: 576px)
		img 
			width: 100% // thm  KH

