.searchbox-wrap
	position: fixed
	z-index: 999
	background: $body 
	top: 0
	left: 0
	right: 0
	bottom: 0
	width: 100%
	height: 100vh
	display: flex
	justify-content: center
	// align-items: center
	opacity: 0
	pointer-events: none
	padding: 15px
	transition: .4s ease-in-out all
	.button-close
		position: absolute
		top: 5px
		right: 5px
		z-index: 11
		color: #333333
		font-size: r(40px)
		cursor: pointer
		transition: .2s ease-in-out all
		&:hover 
			color: $main
	&.active
		opacity: 1
		pointer-events: auto
		background: rgba($body, 1)
	> .Module 
		width: 100%
	@media(max-width: 576px)
		// top: 65px
		// height: calc(100vh - 65px)
		.button-close
			font-size: r(30px)
.searchbox
	display: flex
	justify-content: center
	align-items: center
	border: 1px solid #333333
	max-width: 570px
	width: 100%
	position: relative
	height: 50px
	// margin-top: 50px
	margin-left: auto 
	margin-right: auto 
	input
		padding: 5px 30px
		height: 50px
		border: none
		background: none
		font-size: r(20px)
		font-weight: 400
		color: #bbbbbb 
		width: 100%
		padding-right: 40px
		&::placeholder
			font-style: italic
	button
		width: 60px
		height: 48px
		background: $main
		border: none
		color: #ffffff
		font-size: r(24px)
		display: inline-flex
		justify-content: center
		align-items: center
		transition: .2s ease-in-out all
		position: absolute
		top: 50%
		transform: translateY(-50%)
		right: 0
		z-index: 11
