.dropdown
	position: relative
	cursor: pointer
	.dropdown-menu
		position: absolute
		top: 150%
		right: 0
		width: max-content
		background: $body
		border: 1px solid $main
		padding: 10px 15px
		opacity: 0
		pointer-events: none 
		transition: .2s ease-in-out all
		z-index: 11
	a 
		transition: .2s ease-in-out all
		&:hover 
			color: $main
	&.active, &:hover
		.dropdown-menu
			top: 100%
			pointer-events: auto 
			opacity: 1
