.section-awards-6
	.wrap-box-awards
		display: grid
		grid-template-columns: repeat(12, 1fr)
		gap: r(30px)
		@media (max-width: 767.98px)
			grid-template-columns: repeat(1, 1fr)
	.col-content
		grid-column: span 5
		@media (max-width: 767.98px)
			grid-column: span 12
	.col-image
		grid-column: span 7
		@media (max-width: 767.98px)
			grid-column: span 12
	.col-content
		.list-tab-awards
			ul
				list-style: none
				padding-left: 0
				display: flex
				flex-direction: column
				gap: r(16px)
				li
					display: flex
					align-items: center
					padding: r(12px) r(24px)
					background-color: #F6F6F6
					transition: .3s all ease-in-out
					&:hover,&.active
						background-color: $color-primary
						.number,.title
							color: white
					.number,.title
						transition: .3s all ease-in-out
					.number
						font-size: r(24px)
						color: $color-primary
						font-weight: bold
						margin-right: r(12px)
					.title
						font-size: r(20px)
						color: $color-primary
						padding-left: r(16px)
	.col-image
		position: relative
		width: 100%
		height: 100%
		.img
			@media (max-width: 767.98px)
				+img-scale(402/711)
			@media (min-width: 768px)
				position: absolute
				top: 0
				left: 0
				width: 100%
				height: 100%
				transition: .3s all ease-in-out
				img
					position: absolute
					top: 0
					left: 0
					width: 100%
					height: 100%
					object-fit: cover
					opacity: 0
					&.active
						opacity: 100