.banner-child
	margin-top: 20px
	.title
		position: relative
		z-index: 9999
	.image
		+img-scale(465/1240)
		background-color: #000
		position: relative
		&::before
			content: ''
			display: block
			position: absolute
			pointer-events: none
			left: 0
			bottom: 0
			top: 0
			right: 0
			z-index: 9
			height: 100%
			width: 100%
			background: rgba(0,0,0,.4)
			transition: .3s all ease-in-out
		img
			opacity: 0.75
		.title
			position: absolute
			z-index: 11
			top: 50%
			left: 50%
			transform: translate(-50%, -50%)
			width: 100%
			padding: 15px
			font-size: r(36px)
			font-weight: 700
			color: #ffffff
			+font-2
			text-align: center
			text-transform: uppercase
			letter-spacing: 2.5px
