.main-slide
	.main-button 
		display: flex
		justify-content: center
		align-items: center
		margin-top: 20px
		.button-prev, .button-next
			display: flex
			justify-content: center
			align-items: center
			cursor: pointer
			em 
				font-size: r(48px)
				color: #333333
				transition: .2s ease-in-out all
			&:hover 
				em 
					color: $main
	.main-pagination
		display: flex
		justify-content: center
		align-items: center
		margin-top: 20px
		.swiper-pagination-bullet
			display: flex
			justify-content: center
			align-items: center
			opacity: 1
			width: 14px
			height: 14px
			border: 2px solid $main
			background: #ffffff
			border-radius: 0
			margin: 0 5px
			&.swiper-pagination-bullet-active
				background: $main
	@media(min-width: 1200px)
		position: relative
		.main-button 
			margin-top: 0
			.button-prev, .button-next
				position: absolute
				top: 50%
				transform: translateY(-50%)
				z-index: 11
				opacity: 0.5
				transition: .2s ease-in-out all
				&:hover 
					opacity: 1
			.button-prev
				right: 100%
			.button-next 
				left: 100%
		.main-pagination
			margin-top: 30px
