.section-awards-1
	.video-wrapper
		position: relative
		&:hover
			.btn-play
				transform: translate(-50%, -50%) scale(1.2)
		.img
			+img-scale(578/1028)
		.btn-play
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%, -50%)
			transition: .2s ease-in-out all
			pointer-events: none
section[class*='section-awards']
	color: $neutral-950