.section-awards-vote
	.swiper-container
		padding: 20px 4px
		@media (max-width: 767.98px)
			padding: 10px 4px
			
	.wrap-flex
		display: flex
		align-items: center
		justify-content: space-between
		flex-wrap: wrap
		@media (max-width: 767.98px)
			flex-direction: column
			align-items: flex-start
			gap: 30px
	.wrap-countdown-time
		@media (max-width: 767.98px)
			width: 100%
			display: flex
			justify-content: center
	.countdown-time
		display: flex
		.countdown-time-item
			display: flex
			align-items: center
			&:not(:first-child)
				&:before
					content: ":"
					display: flex
					align-items: center
					font-size: r(20px)
					color: $color-primary-3
					font-weight: bold
					margin: 0 r(4px)
		p
			display: flex
			flex-direction: column-reverse
			align-items: center
			justify-content: center
			position: relative
			span.count-text
				color: $color-primary
				font-size: r(13px)
				font-weight: bold 
				text-transform: uppercase
				position: absolute
				bottom: 100%
			span.count-number
				font-size: r(20px)
				color: white
				font-weight: bold
				width: r(40px)
				height: r(40px)
				background-color: $color-primary-3
				display: flex
				align-items: center
				justify-content: center
				border-radius: r(4px)
	.item-slide
		box-shadow: 0px 4px 40px 0px rgba(0, 0, 0, 0.04)
		.img
			+img-scale(267/400)
		.content
			display: flex
			flex-direction: column
			padding: r(12px) r(20px)
			text-align: center
			align-items: center
			border: 1px solid #EFEFEF
			border-top: 0px
			.title
				font-size: r(20px)
				font-weight: bold
			.line
				width: r(60px)
				height: r(1px)
				background-color: $color-primary-3
				margin: r(12px) 0
			.desc
				font-size: r(14px)
				color: #656565
			.vote
				color: $color-primary-3
				font-size: r(14px)
				margin-top: r(12px)
.popup-modal-award-cate
	@media (max-width: 767.98px)
		padding: r(20px)
	.wrap-form
		display: flex
		flex-direction: column
		gap: r(20px)
		.form-group
			display: flex
			flex-direction: column
			label
				font-size: r(18px)
				display: flex
				align-items: center
				gap: 5px
				margin-bottom: r(12px)
			input:not([type="file"]):not([type="button"]):not([type="submit"]), textarea
				background: #f6f6f6
				border: 1px solid #EFEFEF
				min-height: r(46px)
				box-shadow: none
				padding: r(10px) r(24px)
				transition: all 0.3s ease
				&:focus,&:hover
					border-color: $color-primary
		.wrap-box-qradios
			display: grid
			gap: r(20px)
			background-color: #F6F6F6
			border: 1px solid #EFEFEF
			padding: r(20px)
			grid-template-columns: 1fr
			@media (min-width: 768px)
				grid-template-columns: 1fr 1fr
			@media (min-width: 1024px)
				grid-template-columns: 1fr 1fr 1fr
				padding: r(40px)
			.box-qradio
				display: flex
				align-items: center
				input
					display: none
					&:checked + label
						&::before
							background-image: url('../img/2025/circle-check-red.svg')
				label
					margin-bottom: 0 !important
					display: flex
					align-items: center
					gap: r(12px)
					font-size: r(18px)
					font-weight: 400
					&::before
						content: ""
						display: block
						width: r(20px)
						height: r(20px)
						background-image: url('../img/2025/circle.svg')
						background-size: contain
						background-repeat: no-repeat
						background-position: center
						flex-shrink: 0 
	.wrap-button-submit 
		display: flex
		align-items: center
		justify-content: space-between