.om-contact
	.container
		.col-12
			padding: 0
	.des 
		font-size: r(18px)
		font-weight: 400
		color: #666666
		margin-top: 20px
	.social-list
		display: flex
		align-items: center
		// margin-top:
		li 
			display: flex
			justify-content: center
			align-items: center
			margin-top: 20px
			margin-right: 20px
			a 
				display: flex
				justify-content: center
				align-items: center
				width: 40px
				height: 40px
				border-radius: 50%
				background: #2c3442
				transition: .2s ease-in-out all
				color: #ffffff
				em 
					font-size: r(20px)
					line-height: 1
				&:hover 
					background: $red
	.box-contact
		border-left: none
		height: 100%
	.pr-20
		@media(min-width: 1200px)
			padding-right: 20px
	.main-form
		margin-top: 20px
		.wrap-form
			>div 
				.qtext
					width: 100%
					flex: 0 0 100%
					max-width: 100%
					input 
						height: 50px
						padding: 5px 20px
						background: #fafafa
						border: 1px solid #e1e1e1
						font-size: 16px
						font-weight: 400
						color: #666666
						&:focus
							box-shadow: none
				.qdropdown
					width: 100%
					flex: 0 0 100%
					max-width: 100%
					padding: 0 5px
					margin-bottom: 20px
					select 
						height: 50px
						padding: 5px 20px
						background: #fafafa
						border: 1px solid #e1e1e1
						font-size: 16px
						font-weight: 400
						color: #666666
						width: 100%
						&:focus
							box-shadow: none
				.qparagraph
					width: 100%
					flex: 0 0 100%
					max-width: 100%
					textarea
						height: 100px
						padding: 10px 20px
						background: #fafafa
						border: 1px solid #e1e1e1
						font-size: 16px
						font-weight: 400
						color: #666666
						&:focus
							box-shadow: none
			.frm-btnwrap
				float: none 
				display: flex
				justify-content: flex-end
				width: 100%
				max-width: 145px
				margin-right: 0
				margin-left: auto 
				.frm-btn
					.frm-btn-submit
						transition: .2s ease-in-out all
						&:hover 
							background: $red 
							color: #ffffff
		@media(min-width: 576px)
			.wrap-form>div 
				.qtext
					display: flex
					
					label
						margin-top: 5px 
						width: 155px
						padding-right: 5px
					input 
						width: calc(100% - 155px)
				.qdropdown
					display: flex
					
					label
						margin-top: 5px 
						width: 155px
						padding-right: 5px
					select 
						width: calc(100% - 155px)
				.qparagraph
					display: flex
					
					label
						margin-top: 5px 
						width: 155px
						padding-right: 5px
					textarea 
						width: calc(100% - 155px)
	.box-contact
		margin-top: 30px
		background: #f8f8f8
		padding: 30px
		.main-title
			font-size: r(18px)
			font-weight: 700
			color: #333333
		.contact-list
			.contact-item
				display: flex
				margin-top: 25px
				.icon
					width: 45px
					min-width: 45px
					em 
						font-size: 28px
						color: #2c3442
				.content
					width: calc(100% - 45px)
				.title 
					font-size: r(18px)
					font-weight: 700
					color: $main
					text-transform: uppercase
				p , address
					font-size: r(18px)
					font-weight: 400
					color: #666666
				a 
					display: inline-flex
					+hover-underline(95%, #666666)
					// margin-right: 15px
				.website 
					a 
						color: #079aef
						+hover-underline(95%, #079aef)
		@media(min-width: 1024px)
			margin-top: 0
			padding: 40px 30px
	.main-maps
		margin-top: 30px
		+iframe-scale(600/1240)
		@media(min-width: 1024px)
			margin-top: 40px
		@media(min-width: 1200px)
			margin-top: 50px
