.section-awards-4
	.wrap-content-main
		display: flex
		flex-wrap: wrap
		.img
			width: 66.66666667%
			position: relative
			img
				@media (min-width: 768px)
					position: absolute
					top: 0
					left: 0
					width: 100%
					height: 100%
					object-fit: cover
			@media (max-width: 1279.98px)
				width: 50%
			@media (max-width: 1023.98px)
				width: 40%
			@media (max-width: 767.98px)
				width: 100%
		.content
			width: 33.33333333%
			background-color: $color-primary-1
			display: flex
			flex-direction: column
			color: white
			padding: r(24px) r(48px)
			justify-content: center
			min-height: 580px
			@media (max-width: 1279.98px)
				width: 50%
			@media (max-width: 1023.98px)
				width: 60%
			@media (max-width: 767.98px) 
				width: 100%
				padding: 30px 15px
				min-height: auto
			.desc
				@media (min-width: 1200px)
					padding-right: r(50px)
			table
				tr
					td
						font-size: 1rem
						vertical-align: top
						padding: r(6px) 0px
						&:first-child
							font-size: 1rem
							font-weight: bold
							padding-right: r(20px)
							white-space: nowrap
		.btn-apply-here
			height: 50px
			padding: 0px r(20px)
			background-color: white
			color: $color-primary
			display: flex
			align-items: center
			justify-content: center
			width: fit-content
			margin-top: r(20px)
			text-transform: uppercase
			transition: .3s all  ease-in-out
			font-family: "Google Sans"
			&:hover
				background-color: $red
				color: white