.om-whaton
	padding-top: 0
	.about-nav
		border-top: none
	.main-title
		margin-top: 30px
	.box-whaton-top
		margin-top: 10px
		@media(min-width: 1280px)
			.news-item.news-item-top
				margin-bottom: 10px
				.caption
					.des
						font-size: r(18px)
						* , > *
							font-size: r(18px)
	.news-item
		margin-top: 20px
		@media(min-width: 1024px)
			&.news-item-bottom
				display: flex
				.image
					height: 145px
					padding-top: 0
					width: 235px
					min-width: 235px
				.caption
					width: calc( 100% - 145px )
					padding-left: 20px
					.title
						margin-top: 0
						line-height: 1.3
						+line(2)
					.des
						+line(3)
			&.news-item-top
				.caption
					.title
						font-size: r(26px)
		// 				height: 72px
		// @media(min-width: 1280px)
		// 	&.news-item-top
		// 		.caption
		// 			.title
		// 				height: 78px
	.banner-full
		margin-top: 20px
		img
			width: 100%
			max-height: 100%
			max-width: 100%
			object-fit: contain
		@media(min-width: 1024px)
			margin-top: 30px
	.box-whaton-bottom
		.news-item
			@media(min-width: 1024px)
				margin-top: 30px
				display: flex
				.image
					height: 200px
					padding-top: 0
					width: 315px
					min-width: 315px
				.caption
					padding-left: 30px
					width: calc( 100% - 315px )
					.title
						margin-top: 0
			@media(min-width: 1200px)
				margin-top: 40px
			@media(min-width: 1280px)
				.caption
					.title
						margin-top: 10px
					.des
						+line(6)
	.banner-qc-list
		margin-top: 20px
		@media(min-width: 1024px)
			margin-top: 30px
		@media(min-width: 1200px)
			margin-top: 40px
