.section-awards-2
	.swiper-pagination
		bottom: 0px !important
		position: static !important
		margin-top: 20px !important
		.swiper-pagination-bullet
			background: #C42607
	.wrap-list-slide
		padding: 0px r(40px)
		@media (max-width: 767.98px)
			padding: 0px 15px
		.item-awards
			&:hover
				.content
					transform: translateY(0px)
					&::before
						opacity: 0.9
					.desc
						opacity: 1
			.img
				+img-scale(100/100)
			.content
				background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, #000 100%)
				position: absolute
				bottom: 0px
				left: 0px
				width: 100%
				padding: r(60px) r(24px) r(40px)
				color: white
				display: flex
				flex-direction: column
				align-items: center 
				text-align: center
				gap: r(20px)
				transform: translateY(calc(var(--height) + 1.0416666667rem))
				transition: .2s ease-in-out all
				@media (max-width: 767.98px)
					padding: r(40px) r(16px) r(20px)
					gap: r(10px)
				&::before
					content: ''
					background-color: #C42607
					width: 100%
					height: 100%
					opacity: 0
					transition: .2s ease-in-out all
					position: absolute
					top: 0px
					left: 0px
					z-index: -1
					pointer-events: none
				.icon
					img
						width: r(40px)
						height: r(40px)
				.text
					font-size: r(18px)
				.desc
					font-size: r(14px)
					opacity: 0
					transition: .2s ease-in-out all

