.section-awards-7
	.wrap-timeline
		position: relative
		&::before
			content: ''
			// background-image: repeating-linear-gradient(90deg, #D9D9D9, #D9D9D9 12px, transparent 12px, transparent 16px)
			// background-position: left bottom
			// background-repeat: repeat-x
			// background-size: 100% 1px
			position: absolute
			bottom: r(20px)
			left: 0
			width: 100%
			height: 1px
			border-bottom: 1px dashed #d9d9d9
	.item-timeline
		padding-bottom: 10px
		&:hover
			.wrap-box
				border: 1px solid $color-primary
				.date
					background-color: $color-primary
					color: white
			.star
				transform: scale(1.2)
		.wrap-box
			display: flex
			flex-direction: column
			padding: r(20px)
			gap: r(20px)
			border: 1px solid $neutral-100
			transition: .3s all ease-in-out
		.img
			+img-scale(164/248)
		.content
			display: flex
			flex-direction: column
			gap: r(10px)
			align-items: center
			text-align: center
			h3
				font-size: r(20px)
				font-weight: bold
			.desc
				color: $neutral-600
				font-size: 14px
			.date
				display: flex
				align-items: center
				justify-content: center
				border: 1px solid $color-primary
				padding: r(6px) r(20px)
				border-radius: r(100px)
				color: $color-primary
				transition: .3s all ease-in-out
				font-weight: 400
				@media (max-width: 767.98px)
					font-size: 16px
		.star
			display: flex
			align-items: center
			justify-content: center
			margin-top: r(26px)
			transition: .3s all ease-in-out
.section-img-banner
	+img-scale(320/1240)