.section-award-form
	.label-group
		font-size: r(24px)
		font-weight: bold
		padding: r(6px) r(24px)
		color: $color-primary
		border-left: 2px solid #C42607
		background: linear-gradient(90deg, rgba(220, 220, 220, 0.20) 0%, rgba(220, 220, 220, 0.00) 100%)
		font-family: $font-secondary
	.wrap-form
		#ctl00_mainContent_ctl02_pnlQuestions
			display: grid
			grid-template-columns: 1fr 1fr
			gap: r(24px)
			margin: 0
			@media (min-width: 1024px)
				gap: r(40px)
		.label-group
			grid-column: 1 / -1
			display: flex
			flex-direction: column
			small
				color: #818181
				display: block
				font-size: r(14px)
				font-weight: 400
		.col-span-full
			grid-column: 1 / -1
		.form-group
			margin-bottom: 0
			padding: 0
			display: flex
			flex-direction: column
			&.qupload
				grid-column: 1 / -1
				.RadAsyncUpload
					width: 100%
					background-color: #F6F6F6
					position: relative
					display: flex
					align-items: center
					justify-content: center
					height: r(140px)
					@media (min-width: 1024px)
						height: r(240px)
				.ruInputs
					position: absolute
					display: flex
					flex-direction: column-reverse
				.RadUpload .ruFileWrap
					height: auto
					position: relative
					li:not([id*=brow])
						position: absolute
					input[type="button"]
						width: 145px
						height: 50px
						background-color: $color-primary
						color: #fff
						text-transform: uppercase
						font-size: r(16px)
						cursor: pointer
					input[type="file"]
						width: 100%
						height: 100%
						cursor: pointer
			&.qdate
				.RadPicker
					width: 100% !important
					border: 1px solid #EFEFEF
					transition: all 0.3s ease
					&:focus,&:hover
						border-color: $color-primary
				td
					background: #f6f6f6
					input
						border: 0px
					&:last-child
						border-right: 1px solid #EFEFEF
			label
				font-size: r(16px)
				margin-bottom: r(10px)
			input:not([type="file"]):not([type="button"]):not([type="submit"]), textarea
				background: #f6f6f6
				border: 1px solid #EFEFEF
				min-height: r(46px)
				box-shadow: none
				padding: r(10px) r(24px)
				transition: all 0.3s ease
				&:focus,&:hover
					border-color: $color-primary
			&.qparagraph
				grid-column: 1 / -1
		.qcheckboxes,.qradio
			grid-column: 1 / -1
			display: flex
			flex-direction: column
			gap: r(24px)
			border: 1px solid #DCDCDC
			background: #FFF
			padding: r(24px) r(16px)
			@media (min-width: 1024px)
				padding: r(40px) r(32px)
			label
				font-size: r(20px)
				font-weight: bold
				margin-bottom: 0
			ul
				list-style: none
				padding-left: 0
				display: grid
				gap: r(12px)
				grid-template-columns: 1fr
				@media (min-width: 768px)
					grid-template-columns: 1fr 1fr 
				@media (min-width: 1024px)
					grid-template-columns: 1fr 1fr 1fr
				li
					display: flex
					align-items: center
					input
						display: none
						&:checked + label
							&::before
								background-image: url('../img/2025/circle-check.svg')
					label
						margin-bottom: 0 !important
						display: flex
						align-items: center
						gap: r(12px)
						font-size: r(16px)
						font-weight: 400
						&::before
							content: ""
							display: block
							width: r(20px)
							height: r(20px)
							background-image: url('../img/2025/circle.svg')
							background-size: contain
							background-repeat: no-repeat
							background-position: center
							flex-shrink: 0 
	.wrap-button-submit
		grid-column: 1 / -1
		display: flex
		align-items: center
		flex-wrap: wrap
		gap: r(12px)
		justify-content: space-between
		.frm-btnwrap
			width: auto !important
		input[type="submit"]
			background: $color-primary
			color: #fff
			text-transform: uppercase
			height: 50px
			padding: 0 r(20px)
	.accept-terms
		display: flex
		align-items: center
		flex-direction: row-reverse
		span
			color: #292929
		input
			display: none
			&:checked + label
				&::before
					background-image: url('../img/2025/square-check.svg')
		label
			margin-bottom: 0 !important
			font-size: r(16px)
			font-weight: 400
			a
				color: #C42607
				font-weight: 700
			&::before
				content: ""
				width: r(20px)
				height: r(20px)
				background-image: url('../img/2025/square.svg')
				background-size: contain
				background-repeat: no-repeat
				background-position: center
				flex-shrink: 0 
				margin-right: r(12px)
				display: inline-block
				transform: translateY(4px)
