.section-awards-12
	background-color: #F6F6F6
	.swiper-container
		padding: 10px 0px
	.item-logo
		// 
		position: relative
		display: block
		height: 0
		overflow: hidden
		padding-top: calc(121/181*100%)
		.logo
			display: flex
			align-items: center
			justify-content: center
			background-color: white
			position: absolute
			top: 0
			left: 0
			width: 100%
			height: 100%
			transition: all 0.3s ease
			border: 1px solid #EFEFEF
			&:hover
				border: 1px solid $red
			img
				max-width: 100%
				max-height: 100%
				object-fit: contain