$color-primary: #272774
$color-primary-1: #2C3442
$color-primary-3: #C42607
$neutral-950: #292929
$neutral-200: #DCDCDC
$neutral-600: #656565
$neutral-100: #EFEFEF
$font-secondary: "Playfair Display"
html
	scroll-behavior: smooth
	scroll-padding-top: var(--header-height)
.section-tailwindcss
	.relative
		position: relative
	.title-40
		font-size: r(40px)
		font-weight: bold
		@media (max-width: 1023.98px)
			font-size: 28px
	.text-primary
		color: $main
	.justify-between
		justify-content: space-between
	.gap-5
		gap: r(20px)
	.font-secondary
		font-family: "Playfair Display"
	.mb-3
		margin-bottom: r(12px)
	.mb-5
		margin-bottom: r(20px)
	&.section-py
		padding: r(80px) 0
		@media (max-width: 1023.98px)
			padding: 40px 0px
		&.pt-0
			padding-top: 0px !important
		&.pb-0
			padding-bottom: 0px !important
	.mx-auto
		margin-left: auto
		margin-right: auto
	.text-xl
		font-size: r(20px)
	.mb-base
		margin-bottom: r(40px)
		@media (max-width: 767.98px)
			margin-bottom: 10px
	.zoom-img
		overflow: hidden
		&:hover
			img
				transform: scale(1.05) translateZ(0)
		img
			transition: .5s all ease-in-out !important
	.format-content
		line-height: 1.3
		@media (max-width: 1023.98px)
			font-size: 16px
	.flex-center
		display: flex
		align-items: center
		justify-content: center
	.title-star-primary
		font-size: r(20px)
		display: flex
		align-items: center
		color: $color-primary
		text-transform: uppercase
		&.text-white
			color: white
		&::before
			content: ''
			display: inline-block
			width: r(26px)
			height: r(26px)
			background: url(../img/2025/star.svg) no-repeat center -2px / 100% 100%
			margin-right: r(10px)
	.stretched-link
		&::before
			content: ''
			position: absolute
			top: 0
			left: 0
			width: 100%
			height: 100%
			z-index: 1
	.shadow-dropdown-light
		box-shadow: 0px 4px 40px 0px rgba(0, 0, 0, 0.04)
	.wrap-button-slide
		display: flex
		align-items: center
		justify-content: space-between
		position: absolute
		width: 100%
		z-index: 10
		gap: r(20px)
		@media (max-width: 1279.98px)
			position: static
			justify-content: center
			margin: r(20px) 0px
		@media (min-width: 1280px)
			height: 1px
			transform: translateY(-50%)
			top: 50%
		.btn
			display: flex
			align-items: center
			justify-content: center
			width: r(40px)
			height: r(40px)
			border: 1px solid #0C0A5A
			transition: .3s all ease-in-out
			cursor: pointer
			&.white
				border-color: white
				&::before
					filter: brightness(0) invert(1)
			&:hover
				background-color: #0C0A5A
				&::before
					filter: brightness(0) invert(1)
			&::before
				content: ''
				background-image: url('../img/2025/chevron-left.svg')
				display: inline-block
				width: 10px
				height: 20px
				background-size: 100% 100%
				transition: .3s all ease-in-out
			&.btn-next
				&::before
					transform: rotate(180deg)
			@media (min-width: 1280px)
				&.btn-prev
					transform: translateX(calc(-100% - 20px))
				&.btn-next
					transform: translateX(calc(100% + 20px))
	.btn-vote
		display: flex
		align-items: center
		justify-content: center
		height: r(50px)
		padding: 0 r(26px)
		background-color: $color-primary-3
		color: white !important
		font-size: r(16px)
		font-weight: bold
		text-transform: uppercase
		transition: .3s all ease-out
		border: 0px
		outline: 0px
		&:hover
			background-color: $color-primary-3
			transform: translateY(-2px) scale(1.05)
.fancybox-slide
	@media (max-width: 1023.98px)
		padding: 15px
.swiper-column-auto
	--mr: 15px
	--spv: 1.3
	@media (min-width: 1200px)
		--mr: 1.25rem
	&[data-time='0']
		.swiper-wrapper
			transition-timing-function: linear
	.swiper-slide.swiper-slide
		overflow: visible
	.swiper-slide
		width: calc(calc(100% - calc(var(--mr) * calc(var(--spv) - 1)))/var(--spv))
		height: auto
		&:not(:last-child)
			margin-right: var(--mr)
	&.auto-2-column
		.swiper-slide
			@media (min-width: 768px)
				--spv: 2
	&.auto-3-column
		.swiper-slide
			--spv: 1
			@media (min-width: 768px)
				--spv: 2.3
			@media (min-width: 1024px)
				--spv: 3
		&.show-half-mobile
			@media (max-width: 767.98px)
				.swiper-slide
					width: 80%
	&.auto-4-column
		.swiper-slide
			--spv: 1
			@media (min-width: 576px)
				--spv: 2.3
			@media (min-width: 1024px)
				--spv: 3.5
			@media (min-width: 1200px)
				--spv: 4
	&.auto-5-column
		.swiper-slide
			@media (min-width: 576px)
				--spv: 2
			@media (min-width: 768px)
				--spv: 3
			@media (min-width: 1024px)
				--spv: 4
			@media (min-width: 1200px)
				--spv: 5
	&.show-half-mobile
		@media (max-width: 575.98px)
			.swiper-slide
				width: 80%
	&.allow-touchMove
		.swiper-slide
			cursor: grab