.searchbox-wrap
	position: fixed
	z-index: 999
	background: $body 
	top: 100px
	left: 0
	right: 0
	bottom: 0
	width: 100%
	height: calc(100vh - 100px)
	display: flex
	justify-content: center
	align-items: center
	opacity: 0
	pointer-events: none
	padding: 15px
	transition: .4s ease-in-out all
	.button-close
		position: absolute
		top: 5px
		right: 5px
		z-index: 11
		color: $darkGray 
		font-size: r(26px)
		cursor: pointer
		transition: .2s ease-in-out all
		&:hover 
			color: $main
	&.active
		opacity: 1
		pointer-events: auto
	@media(max-width: 576px)
		top: 65px
		height: calc(100vh - 65px)
.searchbox
	display: flex
	justify-content: center
	align-items: center
	border: 1px solid $darkGray 
	max-width: 540px
	width: 100%
	position: relative
	// padding: 0 15px
	input 
		// width: 200px
		padding: 0 15px
		height: 40px
		border: none
		background: $body
		font-size: r(16px)
		font-weight: 400
		color: $darkGray 
		width: 100%
		padding-right: 40px
		&::placeholder
			font-style: italic
	button
		width: 38px
		height: 38px
		background: $body
		border: none
		color: $darkGray
		font-size: r(20px)
		display: inline-flex
		justify-content: center
		align-items: center
		transition: .2s ease-in-out all
		position: absolute
		top: 50%
		transform: translateY(-50%)
		right: 0
		z-index: 11
